<template>
  <div class="index">
    <div class="header">
      <img class="logo" src="../../assets/img/logo.png" alt="logo">
      <p class="title">精惠投</p>
      <router-link :to="{ path: '/login' }">
        <div class="login">登录</div>
      </router-link>
    </div>
    <div class="index-content index-content-one">
      <div class="option-info">
        <p>O2O优惠券精准投放系统</p>
        <p>对优惠券发放提供合理化建议,为精准投放提供解决方案。</p>
        <div class="info-btn" v-on:click="scroll">立即入驻</div>
      </div>
    </div>
    <div class="index-content index-content-two">
      <div class="index-content-two-title">
        <h3>立即入驻精惠投预测平台</h3>
      </div>
      <div class="index-content-bg">
        <div class="index-content-bg-img"></div>
        <div class="index-content-bg-mask"></div>
      </div>
      <div class="register-box">
        <div class="item">
          <span class="icon icon-company"></span>
          <span class="input"><input type="text" v-model="form.username" size="40" placeholder="公司名称"></span>
        </div>
        <div class="item">
          <span class="icon icon-name"></span>
          <span class="input"><input type="text" v-model="form.nickname" value="" size="40" placeholder="姓名"></span>
        </div>
        <div class="item">
          <span class="icon icon-email"></span>
          <span class="input"><input type="email" v-model="form.email" value="" size="40" placeholder="邮件"></span>
        </div>
        <div class="item">
          <span class="icon icon-phone"></span>
          <span class="input"><input type="tel" v-model="form.phone_number" value="" size="40" placeholder="电话"></span>
        </div>
        <div class="item">
          <span class="icon icon-lock"></span>
          <span class="input"><input type="password" v-model="form.password" value="" size="40" placeholder="密码"></span>
        </div>
        <div class="item">
          <span class="icon icon-lock"></span>
          <span class="input"><input type="password" v-model="form.password_confirm" value="" size="40" placeholder="确认密码"></span>
        </div>
        <button class="submit" v-on:click="register">提交</button>
      </div>
    </div>
  </div>
</template>



<script>
export default {
  data: function () {
    return {
      form: {
        username: undefined,
        nickname: undefined,
        email: undefined,
        phone_number: undefined,
        password: undefined,
        password_confirm: undefined
      }
    }
  },
  methods: {
    scroll(event) {
      var $index = document.querySelector('.index')
      var viewportHeight = window.innerHeight
      var speed = viewportHeight / 15
      var timer = setInterval(function () {
        $index.scrollTop = $index.scrollTop + speed
        if ($index.scrollTop == viewportHeight) {
          // 清除动画定时器
          clearInterval(timer)
        }
      }, 1000 / 60)
    },
    register() {
      var that = this
      var object = this.form
      for (var key in object) {
        if (!object[key]) {
          this.$alert('您的信息不完整', '提示', {
            confirmButtonText: '我知道了'
          })
          return
        }
      }
      if (!/\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/.test(this.form.email)) {
        this.$alert('请输入正确的邮箱地址', '提示', {
          confirmButtonText: '我知道了'
        })
        return
      }
      if (this.form.nickname.length < 5 || this.form.username < 5) {
        this.$alert('用户名或公司名称必须大于五个字符', '提示', {
          confirmButtonText: '我知道了'
        })
        return
      }
      if (this.form.password != this.form.password_confirm) {
        this.$alert('两次密码不一致', '提示', {
          confirmButtonText: '我知道了'
        })
        return
      }
      if (this.form.password.length < 8) {
        this.$alert('您的密码太短啦', '提示', {
          confirmButtonText: '我知道了'
        })
        return
      }
      var obj = Object.assign({}, this.form)
      this.$api.register(obj).then(function (res) {
        if (res.state == 200) {
          that.$alert('注册成功，快去登录吧', '提示', {
            confirmButtonText: '我知道了'
          })
          // 重置表单
          that.resetForm()
        } else {
          that.$alert('系统错误，请重试', '提示', {
            confirmButtonText: '我知道了'
          })
          return
        }
      })
    },
    resetForm() {
      var object = this.form
      for (var key in object) {
        object[key] = ''
      }
    }
  }
}
</script>

<style lang="less" scoped>
.index {
  overflow-y: scroll;
  width: 100%;
  height: 100%;
}

.header {
  position: fixed;
  height: 80px;
  width: 100%;
  background-color: rgba(255, 255, 255, .2);
  z-index: 1;
  .logo {
    float: left;
    margin: 17px 0 17px 150px;
    height: 46px;
  }
  .title {
    float: left;
    font-size: 28px;
    margin-left: 20px;
    color: #fff;
    line-height: 80px;
    letter-spacing: .2em;
  }
  .login {
    position: absolute;
    top: 20px;
    right: 150px;
    width: 118px;
    height: 38px;
    border: 1px solid #fff;
    border-radius: 3px;
    text-align: center;
    line-height: 38px;
    color: #fff;
    transition: all .4s ease-in-out;
    &:hover {
      cursor: pointer;
      border-color: #f45b69;
      color: #f45b69;
    }
  }
}

.index-content {
  position: relative;
  width: 100%;
  height: 100%;
}

.index-content-one {
  background: url('../../assets/img/background.jpg') center center / auto 980px no-repeat #fff;
}

.option-info {
  position: absolute;
  top: 50%;
  margin-top: -100px;
  width: 100%;
  text-align: center;
  >p {
    margin: 24px 0;
    font-size: 30px;
    color: #fff;
    font-weight: 500;
  }
  .info-btn {
    position: absolute;
    left: 50%;
    margin-left: -100px;
    margin-top: 24px;
    border: 2px solid #fff;
    width: 196px;
    height: 46px;
    border-radius: 10px;
    font-size: 20px;
    line-height: 46px;
    color: #fff;
    transition: all .4s;
    &:hover {
      cursor: pointer;
      color: #f45b69;
      border-color: #f45b69;
    }
  }
}

.index-content-two {
  .index-content-bg {
    position: absolute;
    top: 0;
    height: 100%;
    width: 100%;
    &-img {
      position: relative;
      top: 0;
      height: 100%;
      width: 100%;
      background: url('../../assets/img/background2.jpg') center center / auto 980px no-repeat #fff;
    }
    &-mask {
      position: absolute;
      top: 0;
      height: 100%;
      width: 100%;
      background-color: rgba(0, 69, 99, 0.7);
    }
  }

  &-title {
    position: absolute;
    width: 100%;
    top: 90px;
    z-index: 1;
    >h3 {
      width: 100%;
      margin: 40px 0 10px;
      font-size: 30px;
      color: #fff;
      text-align: center;
    }
  }

  .register-box {
    position: absolute;
    top: 180px;
    left: 0;
    right: 0;
    font-size: 14px;
    .item {
      position: relative;
      margin: 0 auto 18px;
      width: 420px;
      height: 45px;
      border-radius: 5px;
      background-color: #fff;
      .icon {
        float: left;
        width: 75px;
        height: 100%;
        &-company {
          background: url(data:image/png;base64,) center no-repeat
        }
        &-email {
          background: url(data:image/png;base64,) center no-repeat
        }
        &-phone {
          background: url(data:image/png;base64,) center no-repeat
        }
        &-name {
          background: url(data:image/png;base64,) center no-repeat
        }
        &-lock {
          background: url(data:image/svg+xml;base64,) center no-repeat;
          background-size: 35px;
        }
      }
      .input {
        >input {
          position: relative;
          float: left;
          width: 345px;
          height: 100%;
          border: 0;
          outline: 0;
          border-radius: 5px;
        }
      }
    }
    .submit {
      margin: 20px auto;
      display: inherit;
      border-style: none;
      transition: all .4s;
      font-size: 16px;
      text-align: center;
      line-height: 45px;
      width: 135px;
      height: 45px;
      border-radius: 10px;
      color: #fff;
      background-color: #f45b69;
      outline: none;
      &:hover {
        cursor: pointer;
        background-color: #f13546;
      }
    }
  }
}
</style>




